<template>
  <form @submit="onSubmit" class="add-form">
    <div class="form-control">
      <label>Search Passage</label>
      <input
        type="text"
        name="zitat"
        v-model="zitat"
        placeholder="Type passage here"
      />
    </div>
    <input
      type="submit"
      value="Start Search"
      class="btn btn-block"
      :disabled="zitat.length === 0"
    />
  </form>
</template>

<script>
export default {
  name: "searchPassage",
  data() {
    return {
      zitat: "",
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      const searchZitat = this.zitat;
      this.$emit("search-for", searchZitat);
    },
  },
  emits: ["search-for"],
};
</script>


<style scoped>
.add-form {
  margin-bottom: 40px;
}
.form-control {
  margin: 20px 0;
}
.form-control label {
  display: block;
}
.form-control input {
  width: 50%;
  height: 40px;
  margin: 5px;
  padding: 3px 7px;
  font-size: 17px;
}
.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.form-control-check label {
  flex: 1;
}
.form-control-check input {
  flex: 2;
  height: 20px;
}
</style>